import React from 'react';
import { default as MuiSlider, SliderProps } from '@mui/material/Slider';

const Slider = React.forwardRef<HTMLElement, SliderProps>( ( props, ref ) => {
	return (
		<MuiSlider
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SliderProps>;

export default Slider;
